.input {
    background: none;
    width: 80%;
    min-height: 300px;
    text-align: center;
    color: #ccc;
    font-size: 16px;
    border: 1px solid #1b98e0;
    outline: none;
}

.form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 20px;
    width: 100%;
}

.btn{
    font-size: 18px;
    padding: 10px;
    width: 80%;
    background-color: #1b98e0;
    color: #eee;
    border: none;
    outline: none;
    cursor: pointer;
    transition: all .3s;

}

.btn:hover {
    background-color: #1b98e088;

}