.item {
    display: grid;
    grid-template-columns: 5fr 2fr 2fr 1fr;
    background-color: #1b98e044;
    text-align: left;
    width: 100%;
    transition: all .2s;
    align-items: center;
}

.item:hover {
    background-color: #00000033;
}

.i{
    padding: 10px ;
}

.i:last-child div{
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

#rm{
    opacity: 0.5;
}