.title{
    font-family: Poppins;
    font-size: 70px;
    text-align: center;
    font-weight: bolder;
}


.content{
    width: 100%;
    padding: 20px;
    height: 500px;
    gap: 20px;
    display: flex;
    flex-direction: column;
}

.create {
    width: 100%;
    background-color: #00000033;
    height: 250px;
    text-align: center;
    padding: 10px;
    display: flex;    
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #1b98e0;
    border-radius: 10px;
}

.form {
    width: 100%;
    margin: 15px 0;
    /* display: flex; */
}

.input {
    width: 100%;
    padding: 10px;
    background: none;
    outline: none;
    font-size: 18px;
    border: 2px solid #1b98e0;
    text-align: center;
    color: #eee;
}
.btn {
    padding: 10px;
    background-color: #1b98e0;
    outline: none;
    border: 2px solid #1b98e0;
    cursor: pointer;
    color: #eee;
    transition: all .3s;
    font-size: 18px;
    width: 100%;
    margin-top: 10px;
    border-radius: 20px;
}

.btn:hover{
    background-color: #1b98e099;
    border: 2px solid #1b98e099;
    transform: scaleY(1.1);

}

.res {
    background-color: #00000088;
    padding: 10px;
    width: 50%;
}

.view {
    width: 100%;
    background-color: #00000033;
    text-align: center;
    padding: 10px;
    align-items: center;
    display: flex;
flex-direction: column;    
}

.list{
    background-color: #00000088;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 800px;
    width: 100%;
    border-radius: 10px;
    overflow: hidden;
}



.headerList{
    display: grid;
    grid-template-columns: 5fr 2fr 2fr 1fr;
    background-color: #1b98e0;
    text-align: left;
    width: 100%;

    
}

.titList {
    padding: 10px;
}

.itens{
    display: flex;
    flex-direction: column;
    width: 100%;
}

