.page {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* 1b98e0 */
.form {
    width: 400px;
    /* height: 400px; */
    background-color: #00000044;
    margin: auto;
    text-align: center;
    padding: 10px;
    border: #1b98e0 1px solid;
    border-radius: 20px;
}

.title {
    font-size: 3rem;
    margin: 10px;
}

.inputs {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.inputs input {
    padding: 15px 10px;
    color: #eee;
    border-radius: 10px;
    outline: none;
    background: none;
    border: 1px solid #1b98e0;
}

.inputs input::placeholder {
    color: #bbb;
}


.btn {
    margin: 20px;
    padding: 10px 2rem;
    color: #eee;
    background-color: #1b98e0;
    outline: none;
    border: 1px solid #1b98e0;
    font-weight: bolder;
    font-size: 1rem;
    transition: all .2s;
    border-radius: 5px;
    cursor: pointer;
}

.btn:hover {
    transform: scale(1.1);
}