.nav {
    height: calc(100vh - 50px);
    width: 250px;
    background-color: #1b98e0;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    transition: all .3s;
    /* position: absolute; */
}

.opt {
    height: 50px;
    width: 100%;
    display: flex;
    cursor: pointer;
    align-items: center;
    padding: 10px;
    font-size: 1rem;
    transition: all .2s;
    text-decoration: none;
    color: #eee;
}

.newNav{
    height: 40px;
    width: 100%;
    display: flex;
    cursor: pointer;
    align-items: center;
    padding: 10px;
    font-size: 1rem;
    transition: all .2s;
    text-decoration: none;
    color: #eee;
    display: flex;
    justify-content: space-between;
}

.newNav:hover{
    background-color: #00000066;

}

.opt:hover {
    background-color: #00000066;
}

#disable {
    width: 0px;
}

#on {
    transition: all .3s;
    background-color: #00000055;
    /* font-weight: bold; */

}

#navOn svg {
    transform: rotateZ(45deg);
    transition: all .3s;
}

#navOnn {
    background-color: #00000022;
}

#navOff svg{
    transition: all .3s;
    transform: rotateZ(0deg);
}

.addNav{
    overflow: hidden;
    width: 100%;
    transition: all .7s;
    height: calc(50px * 9);
}

#navAddOff {
    height: 0px;
    transition: all .3s;

}