.nav {
    width: 100%;
    min-height: 60px;
    /* background-color: black; */
    display: flex;
    break-inside: auto;
    align-items: center;
    justify-content: start;
    border-bottom: 1px solid #1b98e0;
    overflow-x: auto;
    overflow-y: hidden;
    z-index: 99;
}

.nav::-webkit-scrollbar {
    height: 2px;
}



.opt {
    padding: 10px;
    margin: 10px;
    color: #eee;
    text-decoration: none;
    transition: all .3s;
    cursor: pointer;
}

#on{
    color: #1b98e0;
}