.page {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
}

.modal {
    margin-top: 15px;
    width: 100%;
    min-height: 600px;
    background-color: #00000044;
    border: #1b98e088 1px solid;
}

.form {
    display: flex;
    width: 80%;
    margin: 10px auto;
    height: 50px;
}

.form input:first-child {
    width: 100%;
    background: none;
    border: #1b98e0 1px solid;
    color: #eee;
    text-align: center;
    outline: none;
}

.form input:last-child {
    border: #1b98e0 1px solid;
    padding: 15px;
    background-color: #1b98e0;
    color: #eee;
    font-weight: bold;
    cursor: pointer;
}

.modalItens {
    width: 100%;
    padding: 10px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;
}

.iten {
    /* height: 300px; */
    width: 100%;
    background-color: #00000044;
    padding: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.iten a {
    color: #eee;
}

.resumo {
    background-color: #00000044;
    padding: 5px;
    color: #ccc;
    font-size: 12px;
}