.page {
    /* text-align: center; */
}

.modal {
    margin-top: 15px;
    width: 100%;
    min-height: 600px;
    background-color: #00000044;
    border: #1b98e088 1px solid;
    text-align: center;
    padding: 20px;
}

.modal h1 {
    margin-bottom: 10px;
}


.modalItens {
    width: 100%;
    padding: 10px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;

}

.iten {
    /* height: 300px; */
    width: 100%;
    background-color: #1b98e088;
    padding: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.iten a {
    color: #eee;
}

.resumo {
    background-color: #00000044;
    padding: 5px;
    color: #ccc;
    font-size: 12px;
}

.itenDep{
    width: 100%;
    background-color: #00000044;
    padding: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}


.status {
    width: 100%;
    background-color: rgb(119, 119, 0);
    padding: 10px;
    margin-top: 10px;
}

#approved {
    background-color: rgb(0, 119, 10);
}