.header {
    width: 100%;
    height: 50px;
    background-color: #1b98e0;
    display: flex;
    justify-content: space-between;
}

.user, .navBtn{
    height: 50px;
    display: flex;
    align-items: center;
    padding: 0px 20px;
    color: #eee;
    transition: all .3s;
}

#active {
    transform: rotateZ(45deg);

}

.user svg {
    cursor: pointer;
}